.videos-container {
    border: 2px solid white;
    .bmw-container, .riot-container, .paskal-container {
        position: relative;
        video {
            width: 100%;   
        }
        .img-container {
            display: flex;
            justify-content: space-around;
            width: 100%;
            position: absolute;
            bottom: 5%;
            left: 0;
            img {
                width: 18%;
                border: 2px solid white;
            }
        }
       
    }   
}