
@font-face {
    font-family: 'Helvetica';
    src: url('../fonts/helvetica_regular.otf') format('opentype');
  }



a {
   color: white;
   text-decoration: none; 
}

.target {
    color:rgb(241,194,50) !important;
}

.header {   
    font-family: 'Helvetica', sans-serif;
    top: 10%;
    right: 5%;
    position: fixed;
    z-index: 20;   
    color: white;
    text-transform: uppercase;
    font-size: 5rem;
    text-shadow: 0 0 10px black;
    font-weight: 100;
    transition: all .5s ease;
    opacity: 0;
    nav {
        display: flex;
        justify-content: space-around;
        a {
            font-size: 1rem;
            text-decoration: none;
            color: white;
            cursor: pointer;
            &:hover {
                color: rgb(241,194,50);
            }
        }
        .nav-home {
            display: none;
        }
    }
}

.header-small {
    top: 5% !important;
    right: 2%;
    opacity: 1;
    transition: all .5s ease;
    .header-title {
        display: block;
        opacity: 1;
        height: 0;
        width: 0;
        overflow: hidden;
    }
    nav {
        a {
            padding: 0 10px;
        }
        .nav-home {
            display: block;
        }
    }
}

@media(max-width: 1400px) {
    .header {
        font-size: 4rem !important;
        top: 5% !important;
    }
}

@media(max-width: 1000px) {
    .header {
        font-size: 3rem !important;
        nav a{
            font-size: .8rem;
        }
    }
}

