@font-face {
    font-family: 'Helvetica';
    src: url('../fonts/helvetica_regular.otf') format('truetype');
  }
  @font-face {
    font-family: 'Helvetica-light';
    src: url('../fonts/helvetica-light-587ebe5a59211.ttf') format('truetype');
  }
  
  body {
    font-family: 'Helvetica', sans-serif;
  }

.home__container {
    overflow: hidden;
    .blur {
        color: rgb(241,194,50);
        position: absolute;
        opacity: 1;
        width: 100%;
        height: 100vh;
        z-index: 100;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: 10rem;
        transition: all 1s ease-in;
        text-transform: uppercase;
        & div {
            font-family: 'Helvetica-light', sans-serif;
            font-weight: lighter;
            width: 51%;
            color: white;
            text-align: end;
            font-size: 2.5rem;
            letter-spacing: 14px;
        }
    }
    img {
        width: 100%;
    }
    .intro-img {
        height: 100vh;
    }
    video {
        width: 100%;
    }
    .lips-img {
        position: relative;
        margin-top: -15px;
        border-top: 5px solid white;
    }
    .slick-slider {
        max-height: 1100px;
        overflow: hidden;
        .slick-item {
            
            img {
                width: 99.7%;
            }
        }
    }
    
}

@media(max-width: 1800px) {
    .intro-img {
        margin-left: -300px;
        width: 130% !important;
    }
}
@media(max-width: 1400px) {
    .home__container{
        .blur {
            font-size: 7rem;
            & div {
                width: 70%;
                font-size: 1rem;
            }
        }
    }
}
@media(max-width: 1200px) {    
    .intro-img {
        margin-left: -400px;
        width: 170% !important;
    }
    .home__container{
        .blur {
            & div {
                width: 80%;
            }
        }
    }
}

@media(max-width: 1000px) {

    .home__container{
        .blur {
            font-size: 5rem;
            & div {
                width: 70%;
            }
        }
    }
}

@media(max-width: 800px) {    
    .intro-img {
        margin-left: -500px;
        width: 200% !important;
    }
    .home__container{
        .blur {
            & div {
                width: 80%;
            }
        }
    }
}
