@font-face {
    font-family: 'Helvetica';
    src: url('../fonts/helvetica_regular.otf') format('truetype');
  }
.bio__container {
    padding: 2rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: black;
    .bio-title {
        font-family: 'Helvetica', sans-serif;
        text-transform: uppercase;
        color: rgb(241,194,50);
        font-size: 5rem;
        letter-spacing: 20px;
        margin-bottom: 1rem;
    }
    .bio-img {
        width: 100%;
        max-width: 1280px;
        margin-bottom: 2rem;
    }
    .bio-description {
        max-width: 1280px;
        color: white;
        .my-passion {
            color: rgb(241,194,50);
        }
        
    }
}

@media(max-width: 1900px) {
    .bio-title {
        margin-top: 100px;
    }
}

@media(max-width: 1400px) {
    .bio__container {
        .bio-title {
            font-size: 4rem;
        }
    }
}

@media(max-width: 1150px) {
    .bio__container {
        .bio-title {
            font-size: 3rem;
        }
    }
}

@media(max-width: 1000px) {
    .bio__container {
        .bio-title {
            font-size: 2.5rem;
            letter-spacing: 10px;
        }
    }
}

