@font-face {
    font-family: 'Helvetica';
    src: url('../fonts/helvetica_regular.otf') format('truetype');
  }

body {
    background-color: black;
}
.contact-container {
    height: 386px;
    padding: 1rem 2rem;
    margin: 6rem 3rem 3rem;
    position: relative;
    font-family: 'Helvatica', sans-serif;
    background: url('../img/riot/RIOT.00_01_00_06_1.png') no-repeat;
    background-size: 100%;
    background-color: black;
    display: flex;
    align-items: end;
    .contact-title {
        text-transform: uppercase;
        color: rgb(241,194,50);
        font-size: 5rem;
        z-index: 10;
    }
}
    
    .info-container {
        width: 50%;
        margin: 0 auto;
        background-color: black;
        transition: all linear .5s;
        opacity: 0;
        display: flex;
        .tell-container {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 50%;
            color: white;
            font-size: 1.5rem;  
            & div {
                margin-bottom: 5px;
            }
        }

        .links-wrapper {
            width: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                width: 50px;
                background-color: white;
                
                
            }
            .insta, .vimeo, .fb {
                transition: all linear .5s;
                opacity: 0;
                margin-right: 1rem;
            }
            
            .imdb {
                font-weight: 700;
                background-color: rgb(241,194,50);
                padding: .5rem 2rem;
                border-radius: 25px;
            }
        }
    }

@media(max-width: 1800px) {
    .info-container {
        width: 100%;
    }
} 
   
@media(max-width: 1500px) {
    .contact-container {
        background: url('../img/riot/RIOT.00_01_00_06.Still026.jpg') no-repeat;
        background-size: cover;
    }
}

@media(max-width: 1300px) {
    .info-container {
        .tell-container {
            font-size: 1rem;
        }
    }    
}

@media(max-width: 1000px) {
    .contact-container {
    .contact-title {
        font-size: 4rem;
    }
}
}
